@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.searchbox {
    margin: 0.5em 0 2em 0;
}

.search-input{
    padding: 1em;
    background-color: whitesmoke;
    font-size: 0.8em;
    border: 1.5px solid rgb(38, 63, 85);
    border-radius: 0.5em;
    font-family: Ubuntu
}
.card-container {
    background-color: white;
    padding: 0;
    margin: 0;
    font-family: Ubuntu, sans-serif;
    color: black;
    padding-bottom: 0.8em;
    border-radius: 0.5em;
    box-shadow: 0 0 1em 0 rgba(0, 0, 0, 0.2);
    transition: all 0.1s;
}

.card-container:hover {
    -webkit-filter:brightness(1.02);
            filter:brightness(1.02);
    transform: translateY(-0.08em);
    box-shadow: 0 0 1em 0 rgba(0, 0, 0, 0.4);
}

.poke-image {
    width: 8.5em;
    height: 8.5em;
    padding: 1em;
    background-color: whitesmoke;
    border-radius: 0.5em;
}

.poke-detail {
    margin: 0 1em;
}

.poke-id{
    margin: 0;
    font-size: 0.75em;
    color: grey;
}

.poke-name{
    margin: 0.5em 0 0.3em 0;
    font-size: 1.5em;
    font-weight: normal;
}

.poke-types ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;

}

.poke-types ul li{
    font-size: 0.7em;
    padding: 0.1em 1em;
    border: 1px solid black;
    border-radius: 0.2em;
    width: 3.5em;
    height: 1.2em;
    text-align: center;
}

.poke-types ul li:first-child{
    margin-right: 5%;
}
.cardlist-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    grid-gap: 1em;
    gap: 1em;
}
.App {
  background: linear-gradient(325deg, #5B6370, #727D90, #B7C5D2, #84ADD7, #C59FAE, #EEE6B8, #CCBAA1, #B7AB9D, #7B675B);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 1.5em 4% 1.5em;
}

h1 {
  font-size: 4em;
  color: #ffdd00f1;
  font-family: Montserrat;
  margin-bottom: 0.25em;
  text-shadow: 0 0 0.1em #3043d4, 0 0 0.15em #3448df, 0 0 0.2em #2a3fdf, 5px 5px 0.25em #3B4CCA;
}

.loading {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.loading h1 {
  margin: 0 1em 0 0;
}
